const dom = require("./controller/dom");
const domStrings = new dom.DomStrings();

const removeAllClass = () => {
  domStrings.search.classList.remove("search__active");
  domStrings.bgImg.classList.remove("main__bg-scale");
  domStrings.main.classList.remove("main__bg-blur");
  domStrings.body.classList.remove("overflow");
};
const toggleClass = () => {
  domStrings.search.classList.toggle("search__active");
  domStrings.bgImg.classList.toggle("main__bg-scale");
  domStrings.main.classList.toggle("main__bg-blur");
  domStrings.body.classList.toggle("overflow");
  document.body.scrollTop = 0;
  document.documentElement.scrollTop = 0;
};
// window.addEventListener("load", removePreloader);

// searh Bar

[domStrings.searchBtn, domStrings.searchClose].forEach((el) => {
  el.addEventListener("click", toggleClass);

  // home Btn

  domStrings.homeBtn.addEventListener("click", removeAllClass);
});

window.addEventListener("scroll", (e) => {
  let position;
  // console.log(window.scrollY);
  // console.log(e.path[1].scrollY);
  position = Math.floor(window.scrollY * -0.6);

  domStrings.bgImg.style.transform = `translateY(${position}px)`;
});
